<template>
  <div class="container">
    <nav-header :leftArrow="!$route.query.browerFirstIn">
      <div v-if="shareShow" class="share-btn" slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div>
    </nav-header>
    <div style="height: .92rem"/>
    <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <div v-else>
      <div class="content">
        <div class="shop-info">
          <div class="suit-title">
            {{detail.packageName}}
          </div>
          <div class="location" @click="openAddr">
            <p/>
            <span>{{location.region}}</span>
          </div>
          <template v-if="detail.detailVos.length">
            <div class="prod-item" v-for="(item, index) in detail.detailVos" :key="index" @click="goProduct(item)">
              <div class="img-row">
                <img class="img" :src="item.imageUrl"/>
                <div class="out-sale" v-if="!item.onSale">已下架</div>
              </div>
              <div style="flex: 1; overflow: hidden;">
                <div class="prod-name">
                  {{item.productFullName}}
                </div>
                <div class="sale-point">
                  {{item.productTitle}}
                </div>
                <div class="flex-row">
                  <div class="price">
                    <span class="symbol">￥</span>
                    <span class="price-int">{{item.storePrice | formatPrice | formatPriceInt}}</span>
                    <span class="price-float">{{item.storePrice | formatPrice | formatPriceFloat}}</span>
                    <span style="margin-left:10px;color:#333;">×{{item.num}}</span>
                  </div>
                  <i v-if="!item.onSale"></i>
                  <span class="tag" v-else-if="item.stockNum > 0">有货</span>
                  <span class="tag-pre" v-else-if="item.bookable">可预订</span>
                  <span class="tag-no-stock" v-else>无货</span>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="buy-row">
        <div class="price-row">
          <div class="price-box">
            <div class="price">
              <span class="symbol">￥</span>
              <span class="price-int">{{detail.packagePrice| formatPrice | formatPriceInt}}</span>
              <span class="price-float">{{detail.packagePrice | formatPrice | formatPriceFloat}}</span>
            </div>
            <img class="price-img" src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_pice_tag.png">
          </div>
          <div class="origin-price">
            <span class="tag">原价总计:</span>
            <span class="symbol">￥</span>
            <span class="price-int">{{detail.storePrice | formatPrice | formatPriceInt}}</span>
            <span class="price-float">{{detail.storePrice | formatPrice | formatPriceFloat}}</span>
          </div>
        </div>
        <div class="button" @click="goOrder" :class="{active: canBuy}">
          <span >一键下单</span>
        </div>
      </div>
    </div>
    <lazy-addr-list ref="addrList" @handleLocation="handleLocation"/>
  </div>
</template>
<script>
// import utils from '@/utils/utils';
// import config from '@/config/index';
import NavHeader from '@/components/header';
import Skeleton from '@/components/skeleton/index.vue';
import { mapGetters } from 'vuex';
import { getSuitDetail } from '@/http/service/shop';
const lazyAddrList = () => import('@/components/addrChooseList/addrList.vue')
const localData = {
  storeId: '',
  packageId: '',
};
/** 店铺套购方案详情 */
export default {
  components: {
    NavHeader,
    Skeleton,
    lazyAddrList
  },
  data() {
    return {
      isSkeletonLoading: false,
      shareShow: this.$util.isSgRn() || this.$util.isHaierApp(),
      addrLoad: false,
      canBuy: true, // 是否可下单
      detail: {
        packageName: '',
        detailVos: [],
        packagePrice: 0,
        storePrice: 0,
      },
    }
  },
  created() {
    localData.storeId = this.$route.query.storeId || '';
    localData.packageId = this.$route.query.packageId || '';
    this.getData();
  },
  watch: {
    location() {
      this.getData();
    }
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  // beforeRouteEnter (to, from, next) {
  //   if (utils.isSgRn()) {
  //     window.postMessage(JSON.stringify({
  //       type: 'goNewWebview',
  //       data: {
  //         newUrl: `${config.HOST}/sgmobile/shopSuitDetail?storeId=${to.query.storeId}&packageId=${to.query.packageId}`,
  //       },
  //     }));
  //   } else {
  //     next();
  //   }
  // },
  methods: {
    getData() {
      const param = {
        storeId: localData.storeId,
        packageId: localData.packageId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        regionId: this.location.districtId,
        streetId: this.location.townshipId,
      };
      getSuitDetail(param).then(res => {
        if (res.success) {
          this.detail = res.data;
          this.canBuy = res.data.detailVos.every(item => item.onSale && (item.stockNum > 0 || item.bookable));
          this.WeChatSecondShare()
        }
      })
    },
    /**
     * 打开地址选择弹窗
    */
    openAddr() {
      if (!this.addrLoad) {
        lazyAddrList().then(() => {
          this.addrLoad = true;
          this.$nextTick(() => {
            this.$refs.addrList.openAddr()
          })
        })
        return;
      }
      this.$refs.addrList.openAddr()
    },
    /**
     * @description 点击选择所在地区 确认关闭回调
     */
    handleLocation (data) {
      if (this.$util.isSgRn()) {
        setTimeout(() => {
          window.postMessage(JSON.stringify({
            type: 'setLocation',
            data: {
              location: this.location,
            }
          }));
        }, 0)
      }
      this.getData();
    },
    doShare() {
      // let currentUrl = location.href;
      // if (this.$util.isSgRn()) {
      //   let splitStr = location.href.includes('?sg_rn_app') ? '?sg_rn_app' : '&sg_rn_app';
      //   currentUrl = location.href.split(splitStr)[0];
      // }
      // let url = currentUrl.indexOf('?') !== -1 ? currentUrl + '&shareId=' + this.mId : currentUrl + '?shareId=' + this.mId
      let shareId = this.$route.query.shareId
      let url = location.href + `&shareId=${shareId || this.mId}`
      this.$util.inAppShare({
        title: this.detail.packageName || '',
        desc: this.$util.isSyBird() ? '定制智慧家 就找三翼鸟' : '海尔智家商城，海尔家电正品保障',
        url,
        thumImage: this.detail.detailVos.length ? this.detail.detailVos[0].imageUrl : ''
      })
    },
    goProduct(prod) {
      this.$router.push(`/goodsDetail?productId=${prod.productId}&visitStore=${localData.storeId || ''}`);
    },
    goOrder() {
      for (const item of this.detail.detailVos) {
        if (item.limitNum && item.num > item.limitNum) {
          this.$toast(`${item.productFullName} 超出商品限购数量`)
          return
        }
      }

      if (!this.canBuy) return;
      this.$router.push({
        name: 'create',
        query: {
          _proList: [],
          ac: '1',
          isExclusive: false,
          storePackageId: this.detail.packageId,
        },
      })
    },
    WeChatSecondShare () {
      this.$store.dispatch('WeChatSecondShare', {
        title: this.detail.packageName || '',
        desc: '海尔智家商城，海尔家电正品保障',
        thumImage: this.detail.detailVos.length ? this.detail.detailVos[0].imageUrl : ''
      })
    }
  },
}
</script>
